.contentcontainer {
    @media only screen and (max-width: 800px) {
        margin-top: 2em;
    }   

    @media only screen and (min-width: 800px) {
        margin-top: 5em;
    }  
    display: flex;
    align-items: center;
    justify-content: center;
}

.content {
    position: relative;
    flex: 1 1 auto;
    
    max-width: 90%;

    display: flex;
    flex-direction: column;
    align-items: center;
}

